import React from 'react';
import './About.css';


function About() {
  return (
    <div className="about">
         <div className="about-heading"> <span>ABOUT ME</span></div>
         <div className="about-content">
         <p>I am a highly competent IT professional with a proven track record in designing websites, networking and managing databases. I have strong technical skills as well as excellent interpersonal skills, enabling me to interact with a wide range of clients. I am eager to be challenged in order to grow and further improve my IT skills. My greatest passion is in life is using my technical know-how to benefit other people and organisations.</p>
         <p>I did my learning from Codeacademy, W3Schools and freeCodeCamp. I am certified from these institutes. I am currently enrolled in many other backend courses and learning various technology. My aim is to become full stack Developer. I am working on various projects at the moment which you can find in Github.</p>
         <div className="btn-container"><button className="btn">GET CV</button></div>    
         </div>
         {/* <div className="closing-line"></div> */}
    </div>
  )

}

export default About;